.experience-card{position:relative;background-color:#fff;box-shadow:rgba(0,0,0,.2) 0px 10px 30px -15px;border-radius:10px;border:1px solid rgba(211,211,211,.397);border-image:linear-gradient(135deg, var(--accent-color), rgba(var(--accent-color-rgb), 0.5)) 1}.experience-card-dark{position:relative;background-color:#171c28;box-shadow:rgba(0,0,0,.2) 0px 10px 30px -15px;border-radius:10px;border:1px solid rgba(211,211,211,.397)}.experience-card:hover{box-shadow:0 8px 24px rgba(var(--accent-color-rgb), 0.4),0 12px 48px rgba(0,0,0,.2);transform:translateY(-4px)}.experience-banner{background-position:center;background-repeat:no-repeat;background-size:150%;background:linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.5));border-radius:10px 10px 0px 0px;-webkit-filter:blur(0px);-moz-filter:blur(0px);-o-filter:blur(0px);-ms-filter:blur(0px);filter:blur(0px);height:11rem;display:flex;align-items:flex-end;justify-content:center;background:linear-gradient(to bottom, rgba(var(--accent-color-rgb), 0.8), rgba(var(--accent-color-rgb), 0.3) 60%, transparent),center/cover no-repeat url()}.experience-blurred_div{position:absolute;background:linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.2));-webkit-filter:blur(5px);-moz-filter:blur(5px);-o-filter:blur(5px);-ms-filter:blur(5px);filter:blur(5px);height:11rem;top:0;left:0;width:100%;border-radius:10px 10px 0px 0px}.experience-div-company{position:absolute;background:rgba(0,0,0,0);height:9rem;top:0;left:0;width:100%;display:flex;align-items:center;justify-content:center}.experience-text-company{text-align:center;padding:1.5rem;margin:0;color:#fff;font-size:25px;text-align:center;font-weight:700;text-overflow:ellipsis;overflow:hidden;white-space:nowrap}.experience-roundedimg{position:absolute;object-fit:cover;left:0;right:0;top:7rem;margin-left:auto;margin-right:auto;width:8rem;height:8rem;box-shadow:0 .5rem 1rem rgba(0,0,0,.3);border-radius:50%}.experience-text-role{text-align:center;color:#000;font-weight:700;font-size:25px;margin-bottom:.75rem;padding-top:1.5rem;line-height:normal}.dark-mode-text{color:#fff !important}.experience-text-date{text-align:center;color:#000;font-size:20px;margin-bottom:1.5rem;padding-top:1rem;font-weight:600}.experience-text-desc{margin-bottom:2rem;font-size:1rem;line-height:1.6}.experience-text-details{padding:2rem;margin-top:2.5rem}.experience-bullets{margin:0;padding:0;list-style:none;font-size:1.2rem}.experience-bullets li{position:relative;padding-left:1.6rem;margin-bottom:1.5rem;line-height:1.4}.experience-bullets li::before{content:"›";position:absolute;left:0;top:0;font-size:1rem;line-height:1.4;color:var(--accent-color)}@media(max-width: 1380px){.experience-text-role{padding-top:.5rem;font-size:22px}.experience-text-company{font-size:22px}.experience-text-date{font-size:18px}.experience-roundedimg{width:6.5rem;height:6.5rem}}@media(max-width: 768px){.experience-text-role{font-size:22px}.experience-text-company{font-size:22px}.experience-text-date{font-size:18px}.experience-text-desc{font-size:16px}}